.grid {
    width: 100%;
    display: block;
    padding: 0;
}

.grid.wide {
    max-width: 1200px;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -8px;
    margin-right: -8px;
}

.row.no-gutters {
    margin-left: 0;
    margin-left: 0;
}

.col {
    padding-left: 8px;
    padding-right: 8px;
}

.row.no-gutters .col {
    padding-right: 0;
    padding-left: 0;
}

